export default {
	data() {
		return {
			phoneHeight:'',
			iswait:false,
			id:0, // 使用 marker点击事件 需要填写id
			title: 'map',
			latitude: 39.909,
			longitude: 116.39742,
			
			eve_stars:[
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
			],
			choice_eve_stars:5,
			
			stars:[
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
			],
			choice_stars:5,
			
			style_stars:[
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
				'../../static/u370.png',
			],
			choice_style_stars:5,
			
			covers: [{
				latitude: 39.909,
				longitude: 116.39742,
				iconPath: '../../../static/location.png'
			}, {
				latitude: 39.90,
				longitude: 116.39,
				iconPath: '../../../static/location.png'
			}],
			pinglun:'',
			eve_s:2,
			isstar:false,
			isassessing:true,
			isassess:true,
			userinfo:[],
			datalist:[],
			id:0,
			
		}
	},
	onLoad(options) {
		// options.id = 3
		this.id = options.id
		// console.log(options)
		uni.getStorage({
			key:'userinfo',
			success:(res)=>{
				this.userinfo = res.data
			}
		})
		this.getdata()
	},
	
	methods: {
		// 房屋颜值
		clis(index){
			let _self = this
			_self.choice_eve_stars = index+1
			for (let i = 0; i < _self.eve_stars.length; i++) {
				if(i<=index){
					_self.eve_stars[i] = '../../static/u370.png'
				}else{
					_self.eve_stars[i] = '../../static/u373.png'
				}
			}
			this.shuaxin()
		},
		cls_stars(index){
			let _self = this
			// return;
			_self.choice_stars = index+1
			for (let i = 0; i < _self.stars.length; i++) {
				if(i<=index){
					_self.stars[i] = '../../static/u370.png'
				}else{
					_self.stars[i] = '../../static/u373.png'
				}
			}
			this.shuaxin()
		},
		cls_style_stars(index){
			let _self = this
			// return;
			_self.choice_style_stars = index+1
			for (let i = 0; i < _self.style_stars.length; i++) {
				if(i<=index){
					_self.style_stars[i] = '../../static/u370.png'
				}else{
					_self.style_stars[i] = '../../static/u373.png'
				}
			}
			this.shuaxin()
		},
		shuaxin(){
			this.isstar = false;
			this.isstar = true;
		},
		getdata(){
			let _self = this
			uni.request({
				url:_self.common.websiteUrl+"experhome_custormers_datadef",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{'id':_self.id,},
				success: (res) => {
					if(res.data.code==200){
						_self.datalist =res.data.data;
					}else{
						_self.common.callback('暂无更多')
					}
				},fail() {
					_self.common.network()
				}
			});
		},
		assessing(){
			let _self = this
			console.log(_self.pinglun)
			console.log(_self.choice_eve_stars)
			console.log(_self.choice_style_stars)
			console.log(_self.choice_stars)
			uni.request({
				url:_self.common.websiteUrl+"experhome_custormers_pinglun",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'id':_self.id,
					'evaluate':_self.pinglun,
					'stars':_self.choice_stars,
					'eve_stars':_self.choice_eve_stars,
					'style_stars':_self.choice_style_stars,
					'status':3
					},
				success: (res) => {
					if(res.data.code==200){
						_self.common.callback('评论成功')
						_self.datalist.status=3
						_self.isstar=false
						_self.isassessing=false
						_self.isassess=false
					}else{
						_self.common.network()
					}
				},fail() {
					_self.common.network()
				}
			});
		},
		assess(){
			this.isstar = true
		},
		close(){
			this.isstar = false
		},
		
		cancel(){
			uni.navigateTo({
			    url: '../cancelappointment/cancelappointment?id='+this.id,
				animationType: 'fade-in',
				animationDuration: 200
			});
		},
		back(){
			uni.navigateTo({
			    url: '../myappointment/myappointment'
			});
		}
	},
	onReady(){
		// 计算屏幕高度 ，宽度
		let _this = this;
		uni.getSystemInfo({
			success: (res) => {
				 _this.phoneHeight = res.windowHeight*0.58;
			}
		});
	},
}
